.error_layout {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;

    background-color: rgba(0,0,0, 0.5);

    display: flex;
    justify-content: center;
    align-items: center;
}

.alert_window {
    padding: 50px;
    border: 3px palevioletred solid;
    border-radius: 5px;
    color: red;
    background-color: pink;

    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
}
.alert_button {
    min-width: 60px;
    max-width: 100px;
    min-height: 25px;
    background-color: brown;
    color: white;
    font-size: 22px;
    border-radius: 10px;
}

.alert_line_div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;

    width: 100%;
    max-height: 35xp;
    background-color: pink;
    color: blue;
    border-radius: 5px;
    font-size: 22px;

    transition: margin-right 1s;
    animation: 3s linear 1s alert_line_animation;
}
@keyframes alert_line_animation {
    0% { background-color: pink; }
    100% { background-color: rgba(0,0,0, 0.5); }
}